import trailerStars from '../../components/trailerStars.vue'
import {request} from '../../common/request.js'
export default {
	data() {
		return {
			carouselList: [],
			hotMovies: [],
			hotTrailers: [],
			guessLikeMovies: [],
			praiseMeAnimates: []
		}
	},
	onLoad() {
		this.getCarouselData()
		this.getHotMovies()
		this.getHotTrailers()
		this.getGuessULikeData()
		
		// 在页面创建时创建一个临时的动画对象
		this.animation = uni.createAnimation()
	},
	onPullDownRefresh() {
		this.getGuessULikeData(() => {
			uni.stopPullDownRefresh()
		})
	},
	onHide() {
		// 页面被隐藏时  暂停播放
		this.videoCtx && this.videoCtx.pause()
	},
	onShow() {
		// 页面被再次显示的时候  继续播放
		this.videoCtx && this.videoCtx.play()
	},
	methods: {
		// 获取轮播图数据
		async getCarouselData() {
			let res = await request({ 
				url: '/index/carousel/list',
				method: 'post'
			})
			if(res.status == 200) {
				this.carouselList = res.data
			}
		},
		// 获取热门超英数据
		async getHotMovies() {
			let res = await request({
				url: '/index/movie/hot',
				method: 'post',
				data: {
					type: 'superhero'
				}
			})
			if(res.status == 200) {
				this.hotMovies = res.data
			}
		},
		// 获取热门预告数据
		async getHotTrailers() {
			let res = await request({
				url: '/index/movie/hot',
				method: 'post',
				data: {
					type: 'trailer'
				}
			})
			if(res.status == 200) {
				this.hotTrailers = res.data
			}
		},
		async getGuessULikeData(callback) {
			let res = await request({
				url: '/index/guessULike',
				method: 'post'
			})
			if(res.status == 200) {
				this.guessLikeMovies = res.data
			}
			callback && callback()
		},
		handlePraiseMe(e) {
			let index = e.currentTarget.dataset.index

			// 构建动画数据  并且通过step表示该组动画完成
			this.$set(this.praiseMeAnimates, index, this.animation.translateY(-70).scale(1.2).opacity(1).step({
				duration: 300
			}).export())
			
			setTimeout(() => {
				this.$set(this.praiseMeAnimates, index, this.animation.translateY(-30).scale(1).opacity(0).step({
					duration: 0
				}).export())
			}, 350);
		},
		handleToDetail(e) {
			let id = e.currentTarget.dataset.movieid

			uni.navigateTo({
				url: '../movie/movie?id=' + id
			})
		},
		// 监听视频播放
		handleVideoPlaying(e) {
			console.log(e)
			let id = e.currentTarget.dataset.playinindex
			
			this.videoCtx = uni.createVideoContext(id)
			
			this.hotTrailers.forEach(item => {

				if(item.id !== id) {
					uni.createVideoContext(item.id).pause()
				}
			})
		}
	},
	components: {
		trailerStars
	},
	onUnload() {
		// 页面卸载的时候清除动画数据
		this.praiseMeAnimates = []
	}
}
